"use client";
import axios from "axios";
import { useEffect, useState } from "react";
import { useRouter, useParams } from "next/navigation";

export default function ProfilePage() {
  const { id } = useParams(); // ✅ Get user ID from URL
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    console.log("User ID from URL:", id); // ✅ Debugging

    async function fetchUser() {
      if (!id) {
        setError("No user ID provided");
        setLoading(false);
        return;
      }

      try {
        const response = await axios.get(`http://localhost:5000/api/getone/${id}`);
        console.log("API Response:", response.data); // ✅ Check API response

        if (response.data) {
          console.log("User found:", response.data);
          setUser(response.data);
        } else {
          console.log("User not found in API response");
          setError("User not found");
        }
      } catch (error) {
        console.error("Error fetching user:", error);
        setError("Failed to fetch user details");
      } finally {
        setLoading(false);
      }
    }

    fetchUser();
  }, [id]);

  if (loading) return <p className="text-center mt-10">Loading...</p>;
  if (error) return <p className="text-center text-red-500 mt-10">{error}</p>;

  return (
    <div className="flex flex-col items-center mt-10 ">
      <h2 className="text-2xl font-bold">User Profile</h2>
      <div className="bg-white p-6 rounded-lg shadow-lg w-auto mt-5">
        <p className="mb-2"><strong>First Name:</strong> {user.fname}</p>
        <p className="mb-2"><strong>Last Name:</strong> {user.lname}</p>
        <p className="mb-2"><strong>Email:</strong> {user.email}</p>
        <p className="mb-2"><strong>Password:</strong> {user.password}</p> {/* ✅ Display password */}

        {/* Buttons */}
        <div className="flex space-x-96 mt-4">
          <button
            onClick={() => router.push(`/start/login/userlogin/updateuser/${id}`)}
            className="bg-green-500 text-white py-2 px-4 rounded hover:bg-green-600"
          >
            Update Profile
          </button>
          <button
            onClick={() => router.push("/start/login/userlogin")}
            className="bg-gray-500 text-white py-2 px-4 rounded hover:bg-gray-600"
          >
            Back
          </button>
        </div>
      </div>
    </div>
  );
}
